h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Lora';
}
h2 {
  position: relative;
  display: inline-flex;
  font-size: 2.25rem;
  line-height: 130%;
  font-weight: 700;
  color: #002B45;
  margin: 0;
  span {
    position: absolute;
    top: 1.375rem;
    right: calc(100% + 1.25rem);
    width: 312.5rem;
    height: 0.0625rem;
    background: #002B45;
  }
}
button {
  font-family: 'OpenSans';
  text-align: center;
  cursor: pointer;
}
.mbtn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-white__outline {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 0.0625rem solid #FFF;
  color: #FFF;
  cursor: pointer;
  transition: all 0.3s;
  span {
    font-family: 'OpenSans';
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
  }
  &:hover {
    border-color: #F2A22E;
    color: #F2A22E;
  }
  &:active,
  &:focus {
    border-color: rgba(255, 255, 255, 0.5);
    color: rgba(255, 255, 255, 0.5);
  }
}
.btn-blue__outline {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: transparent;
  border: 0.0625rem solid #002B45;
  color: #002B45;
  cursor: pointer;
  transition: all 0.3s;
  span {
    font-family: 'OpenSans';
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
  }
  &:hover {
    border-color: #F2A22E;
    color: #F2A22E;
  }
  &:active,
  &:focus{
    border-color: #011A2A;
    color: #011A2A;
  }
}
.btn-blue {
  background: #002B45;
  span {
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
    color: #FBF6EF;
  }
  &:hover {
    background: #015B91;
  }
  &:active,
  &:focus {
    background: #011A2A;
  }
}
.slick-prev {
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url("../img/prev.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  svg {
    display: none;
  }
}
.slick-next {
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1.1875rem;
    height: 0.4375rem;
    background-image: url("../img/next.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  svg {
    display: none;
  }
}


@media screen and (max-width: 992px) {
  h2 {
    font-size: 2rem;
    span {
      top: 1.25rem;
    }
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.75rem;
    span {
      top: 1.125rem;
    }
  }
}