.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #ffffff;
  padding: 18.125rem 0 10rem 0;
  &__inner {
    position: relative;
  }
  &__wrap {
    max-width: 45.625rem;
  }
  h1 {
    font-size: 4rem;
    line-height: 130%;
    font-weight: 400;
  }
  &__price {
    display: flex;
    align-items: center;
    margin-bottom: 3.5rem;
    &-icon {
      flex-shrink: 0;
      width: 4.375rem;
      margin-right: 2rem;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 1.75rem;
      line-height: 130%;
    }
    &-text span {
      font-weight: 700;
    }
  }
  &__info {
    position: relative;
    display: inline-flex;
    margin-bottom: 0.9375rem;
    span {
      font-size: 0.875rem;
      line-height: 130%;
    }
    i {
      position: absolute;
      top: 0.5625rem;
      right: -2.625rem;
      width: 2.375rem;
      img {
        width: 100%;
      }
    }
  }
  &__btn {
    width: 12.6875rem;
    height: 3.625rem;
  }
  &__advantage {
    position: relative;
    min-height: 5.375rem;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 43, 69, 0.90);
    backdrop-filter: blur(0.125rem);
    border-radius: 0.9375rem;
    padding: 0.1875rem 2rem;
    &-text {
      font-size: 1.125rem;
      line-height: 130%;
    }
    &-icon {
      position: absolute;
      top: 50%;
      right: -1rem;
      transform: translate(0, -50%);
      width: 2rem;
      img {
        width: 100%;
      }
    }
  }
  &__advantage:nth-child(1) {
    position: absolute;
    top: -6.875rem;
    left: 24.6875rem;
  }
  &__advantage:nth-child(2) {
    position: absolute;
    top: 2.5rem;
    left: 50.625rem;
  }
  &__advantage:nth-child(3) {
    position: absolute;
    top: 15rem;
    left: 46.25rem;
  }
}
.advantages {
  padding: 0 0 2.1875rem 0;
  margin-top: -2.5rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    margin-left: -9.375rem;
  }
  &__left {
    width: 26.25rem;
  }
  &__right {
    position: relative;
    width: calc(100% - 26.25rem);
    display: flex;
    align-items: center;
    padding-left: 5.625rem;
    &:after {
      content: '';
      position: absolute;
      top: 2.5rem;
      left: 0;
      width: 312.5rem;
      height: calc(100% - 7.5rem);
      background: #FBF6EF;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__desc {
    position: relative;
    z-index: 2;
    padding: 6.25rem 0;
    margin-top: -2.5rem;
  }
  h2 {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 1.375rem;
      left: calc(100% + 1.25rem);
      width: 312.5rem;
      height: 0.0625rem;
      background: #002B45;
    }
  }
  &__items {
    display: flex;
    justify-content: space-between;
    gap: 4.375rem;
    margin-top: 3.5rem;
  }
  &__item {
    display: flex;
    &-icon {
      flex-shrink: 0;
      width: 4.375rem;
      margin-right: 1.875rem;
      img {
        width: 100%;
      }
    }
    &-text {
      max-width: 16.875rem;
      font-size: 1.5rem;
      line-height: 130%;
      color: #002B45;
      span {
        display: block;
        font-weight: 700;
      }
    }
  }
}
.works {
  position: relative;
  z-index: 4;
  padding: 2.1875rem 0 25rem 0;
  &:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 70rem;
    background: #002B45;
    pointer-events: none;
  }
  .container-fluid {
    position: relative;
    z-index: 2;
  }
  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-top: 3.125rem;
  }
  .close-menu {
    display: none;
  }
  &__selections {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-right: 3.125rem;
    margin-bottom: 1.5rem;
  }
  .select__wrap {
    position: relative;
    width: 13.125rem;
  }
  .select__wrap--active {
    .select__list-default {
      color: #ffffff;
      background: #002B45;
    }
    .select-arrow {
      svg {
        transform: rotate(-180deg);
      }
    }
    .select__list {
      opacity: 1;
      pointer-events: all;
    }
  }
  .select__list-default {
    height: 3.625rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #002B45;
    background: #ffffff;
    border: 0.0625rem solid #002B45;
    padding: 0.1875rem 1.25rem;
    cursor: pointer;
    transition: all 0.3s;
  }
  .select__current {
    font-size: 1rem;
    line-height: 130%;
    text-transform: uppercase;
    input {
      display: none;
    }
  }
  .select-arrow {
    flex-shrink: 0;
    width: 1.4375rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 0.625rem;
    svg {
      width: 100%;
      height: auto;
      transition: all 0.3s;
    }
    svg path {
      fill: currentColor;
    }
  }
  .select__list {
    position: absolute;
    z-index: 3;
    top: 100%;
    left: 0;
    width: 100%;
    padding-top: 1.25rem;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  .select__checkboxes {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    background: #FFF;
    box-shadow: 0 0.25rem 0.4375rem 0.375rem rgba(0, 0, 0, 0.05);
    padding: 1.5625rem 1.25rem;
  }
  .select__check {
    position: relative;
    max-width: 13.125rem;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      &:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      position: relative;
      padding-left: 1.5rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.3125rem;
        left: 0;
        width: 0.875rem;
        height: 0.875rem;
        background: transparent;
        border: 0.0625rem solid #002B45;
        border-radius: 0.1875rem;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0.1875rem;
        width: 0.5rem;
        height: 0.5rem;
        background: #002B45;
        border-radius: 0.1875rem;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-bottom: 1.5rem;
  }
  &__btn {
    height: 3.625rem;
    background: #E1E1E1;
    padding: 0.1875rem 2rem;
    border: none;
    span {
      font-size: 1rem;
      line-height: 130%;
      text-transform: uppercase;
      color: #002B45;
    }
    &:hover {
      background: #cbcbcb;
    }
  }
  &__btn2 {
    height: 3.625rem;
    background: #ffffff !important;
    padding: 0.1875rem 2rem;
  }
  &__filter {
    display: none;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    margin-top: 2.5rem;
    span {
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      color: #002B45;
    }
    i {
      flex-shrink: 0;
      width: 2rem;
      margin-left: 1rem;
      img {
        width: 100%;
      }
    }
  }
  &__cards {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 3rem / 2));
    gap: 3rem;
    margin-top: 1.5625rem;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -6.875rem;
      width: 34.375rem;
      height: 15.9375rem;
      border: 0.0625rem solid #FBF6EF;
      pointer-events: none;
    }
  }
  &__link {
    position: relative;
    z-index: 2;
    width: 13.75rem;
    height: 3.625rem;
    background: #002B45 !important;
    margin: 3.625rem auto 0;
  }
  &__lines {
    span:nth-child(1) {
      position: absolute;
      top: -0.625rem;
      right: -0.625rem;
      width: 47.8125rem;
      height: 20.3125rem;
      border: 0.0625rem solid #002B45;
      pointer-events: none;
    }
  }
  &__text-icon {
    position: absolute;
    z-index: 1;
    right: 1.75rem;
    bottom: 3rem;
  }
}
.works-card {
  display: flex;
  align-items: center;
  color: #002B45;
  &__slider {
    position: relative;
    z-index: 2;
    width: 17.9375rem;
  }
  &__desc {
    position: relative;
    width: calc(100% - 17.9375rem);
    padding: 3.5rem 2.8125rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: calc(100% + 8.125rem);
      height: 100%;
      background: #FBF6EF;
      border: 0.0625rem solid #000;
      pointer-events: none;
    }
  }
  &__wrap {
    position: relative;
    z-index: 2;
  }
  &__title {
    font-family: 'Lora';
    font-size: 1.5rem;
    line-height: 130%;
    font-weight: 700;
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    margin-top: 1rem;
  }
  &__prices {
    display: flex;
    flex-wrap: wrap;
    gap: 1.875rem;
    margin-top: 2.8125rem;
  }
  &__price {
    position: relative;
    font-size: 1rem;
    line-height: 130%;
    padding-right: 1.875rem;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        width: 0.125rem;
        height: 100%;
        background: #D9D9D9;
        border-radius: 0.375rem;
      }
    }
    span {
      display: block;
    }
    span:nth-child(1) {
      font-weight: 700;
    }
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
    color: #7C7C7C;
    margin-top: 1rem;
    span:nth-child(1) {
      font-weight: 700;
    }
  }
  &__btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    margin-top: 2.8125rem;
  }
  &__btn {
    width: 10rem;
    height: 3.625rem;
  }
  &__btn2 {
    flex-grow: 1;
    height: 3.625rem;
  }
  .big-slider {
    .works-card__img {
      position: relative;
      padding-bottom: 126%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
  .small-slider {
    background: #001623;
    padding: 0.75rem 1.25rem 0.75rem 4.6875rem;
    .slick-slide {
      &:not(:last-child) {
        margin-right: 0.375rem;
      }
    }
    .works-card__img {
      position: relative;
      padding-bottom: 90%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
  .slick-list {
    padding: 0 !important;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    div:first-child {
      height: 100%;
    }
  }
  &__img {
    height: 100%;
    display: block !important;
  }
  .slick-prev {
    top: 50%;
    left: 1rem;
    transform: translate(0, -50%);
    width: 1.125rem;
    height: auto;
    box-shadow: unset;
    padding: 0;
    &:after {
      width: 1.1875rem;
      height: 0.4375rem;
    }
  }
  .slick-next {
    top: 50%;
    left: 2.5rem;
    transform: translate(0, -50%);
    width: 1.125rem;
    height: auto;
    box-shadow: unset;
    padding: 0;
    &:after {
      width: 1.1875rem;
      height: 0.4375rem;
    }
  }
  .slick-dots {
    display: none !important;
  }
}
.section-wrap {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);
    width: 100%;
    height: calc(100% - 15.625em);
    background: #FBF6EF;
  }
}
.calculator {
  position: relative;
  z-index: 5;
  padding: 0 0 3.125rem 0;
  margin-top: -18.75rem;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background: #FFF;
    box-shadow: 0 0.9375rem 2rem 0 rgba(77, 77, 77, 0.10), 0 3.625rem 3.625rem 0 rgba(77, 77, 77, 0.09), 0 8.1875rem 4.9375rem 0 rgba(77, 77, 77, 0.05), 0 14.5625rem 5.8125rem 0 rgba(77, 77, 77, 0.01), 0 22.6875rem 6.25rem 0 rgba(77, 77, 77, 0.00);
    border-radius: 1.25rem;
  }
  &__img {
    width: 50.625rem;
    img {
      width: 100%;
      height: 100%;
      border-radius: 1.25rem 0 0 1.25rem;
    }
  }
  &__desc {
    width: calc(100% - 50.625rem);
    display: flex;
    flex-direction: column;
    color: #002B45;
    padding: 3.5rem 2rem 3.5rem 6.25rem;
  }
  &__subtitle {
    font-size: 2rem;
    line-height: 130%;
    margin-top: 2rem;
  }
  &__btn {
    width: 10.75rem;
    height: 3.625rem;
    margin-top: 3rem;
  }
  &__info {
    font-size: 1rem;
    font-weight: 700;
    margin-top: 1.25rem;
  }
  &__discount {
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
    &-icon {
      flex-shrink: 0;
      width: 4.375rem;
      margin-right: 0.3125rem;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      span {
        font-size: 1.5rem;
        font-weight: 700;
      }
    }
  }
}
.info {
  padding: 3.125rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 57.5rem;
    margin-right: 1rem;
  }
  &__right {
    width: calc(100% - 58.5rem);
  }
  &__quote {
    background-image: url("../img/quote-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border: 0.9375rem solid #FFF;
    padding: 5rem 6.25rem;
    margin-top: 3.125rem;
    &-text {
      position: relative;
      max-width: 40rem;
      font-size: 1.5rem;
      line-height: 130%;
      font-weight: 700;
      color: #002B45;
      &:after {
        content: '“';
        position: absolute;
        top: -2rem;
        left: -2rem;
        width: auto;
        height: auto;
        font-size: 13.75rem;
        line-height: 60%;
        color: #ABABAB;
        opacity: 0.37;
        pointer-events: none;
      }
    }
  }
  &__form {
    max-width: 43.75rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.1875rem solid #002B45;
    padding: 1.75rem 2rem;
    margin-top: 5rem;
    &-desc {
      max-width: 25.625rem;
      color: #002B45;
    }
    &-title {
      font-size: 1rem;
      line-height: 130%;
      font-weight: 700;
    }
    &-text {
      font-size: 1rem;
      line-height: 130%;
      margin-top: 1rem;
    }
    &-socials {
      display: flex;
      align-items: center;
      gap: 1rem;
      margin-left: 2rem;
    }
    &-social {
      width: 2.25rem;
      height: 2.25rem;
      svg {
        width: 100%;
        height: 100%;
      }
      svg path {
        transition: all 0.2s;
      }
      &:hover {
        svg path:nth-child(1) {
          fill: #011A2A;
        }
      }
    }
  }
  &__wrap {
    position: relative;
  }
  &__images {
    img {
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      left: -18.75rem;
      bottom: 0;

    }
  }
  &__staff {
    position: absolute;
    bottom: 0;
    left: -14.375rem;
    width: 100%;
    display: flex;
    justify-content: center;
    background: #002B45;
    padding: 2.8125rem;
    &-title {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 700;
      color: #ffffff;
    }
    &-text {
      font-size: 1.25rem;
      line-height: 130%;
      color: #ffffff;
      margin-top: 0.3125rem;
    }
  }
}


@media screen and (max-width: 1700px) {
  .advantages {
    &__right {
      padding-left: 2.5rem;
    }
    &__items {
      gap: 1.875rem;
    }
  }
  .info {
    &__inner {
      align-items: center;
    }
    &__images {
      img:nth-child(2) {
        left: -6.25rem;
      }
    }
    &__staff {
      left: 0;
    }
  }
}

@media screen and (max-width: 1600px) {
  .calculator {
    &__inner {
      align-items: center;
      background: transparent;
      box-shadow: unset;
    }
    &__img {
      width: 31.25rem;
    }
    &__desc {
      width: calc(100% - 31.25rem);
      background: #FFF;
      box-shadow: 0 0.9375rem 2rem 0 rgba(77, 77, 77, 0.10), 0 3.625rem 3.625rem 0 rgba(77, 77, 77, 0.09), 0 8.1875rem 4.9375rem 0 rgba(77, 77, 77, 0.05), 0 14.5625rem 5.8125rem 0 rgba(77, 77, 77, 0.01), 0 22.6875rem 6.25rem 0 rgba(77, 77, 77, 0.00);
      border-radius: 0 1.25rem 1.25rem 0;
      padding: 2.5rem 2rem;
    }
  }
  .info {
    &__left {
      width: calc(100% - 30.625rem);
      margin-right: 0;
    }
    &__right {
      width: 28.125rem;
      margin-left: 2.5rem;
    }
  }
}

@media screen and (max-width: 1500px) {
  .advantages {
    &__item {
      &-icon {
        width: 3.125rem;
        margin-right: 1.25rem;
      }
      &-text {
        font-size: 1.375rem;
      }
    }
  }
  .works-card {
    &__desc {
      padding: 2rem;
    }
  }
}

@media screen and (max-width: 1400px) {
  .advantages {
    &__inner {
      margin-left: -5rem;
    }
    &__left {
      width: 18.75rem;
    }
    &__right {
      width: calc(100% - 18.75rem);
      &:after {
        height: calc(100% - 5rem);
      }
    }
    &__desc {
      padding: 5rem 0;
      margin-top: 0;
    }
  }
  .works {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 2rem / 2));
      gap: 2rem;
    }
  }
  .works-card {
    &__slider {
      width: 13.75rem;
    }
    &__desc {
      width: calc(100% - 13.75rem);
      padding: 1rem;
      &:after {
        width: calc(100% + 2.5rem);
      }
    }
    &__prices {
      gap: 1rem;
    }
    &__price {
      padding-right: 1rem;
    }
  }
  .info {
    h2 {
      br {
        display: none;
      }
    }
    &__quote {
      padding: 3.75rem 2rem;
      margin-top: 2.5rem;
    }
    &__form {
      margin-top: 2.5rem;
    }
    &__staff {
      padding: 2rem;
    }
  }
}

@media screen and (max-width: 1250px) {
  .main {
    &__advantage:nth-child(2) {
      left: 45rem;
    }
  }
}

@media screen and (max-width: 1181px) {
  .main {
    padding-bottom: 6.25rem;
    h1 {
      font-size: 3.25rem;
    }
    &__price {
      &-icon {
        width: 2rem;
        margin-right: 1.25rem;
      }
      &-text {
        font-size: 1.5rem;
      }
    }
    &__advantage:nth-child(1) {
      left: 3rem;
    }
    &__advantage:nth-child(2) {
      top: -6.875rem;
      left: unset;
      right: 3rem;
    }
    &__advantage:nth-child(3) {
      left: unset;
      right: 5rem;
    }
  }
  .advantages {
    &__right {
      padding-left: 1.875rem;
    }
    &__items {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1.875rem / 2));
    }
  }
  .works-card {
    display: block;
    &__slider {
      width: 100%;
    }
    &__desc {
      width: 100%;
      &:after {
        width: 100%;
      }
    }
  }
