.header {
  position: absolute;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 43, 69, 0.90);
  color: #ffffff;
  padding: 2.1875rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
    margin-right: 2rem;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    width: 11.0625rem;
    display: block;
    margin-right: 2rem;
    img {
      width: 100%;
    }
  }
  &__yandex {
    display: flex;
    align-items: center;
    &-icon {
      flex-shrink: 0;
      width: 3.4375rem;
      margin-right: 0.3125rem;
      img {
        width: 100%;
      }
    }
    &-title {
      font-size: 13.826px;
      line-height: 130%;
      font-weight: 700;
    }
    &-rating {
      display: flex;
      align-items: center;
      margin-top: 0.125rem;
    }
    .stars {
      display: flex;
      align-items: center;
    }
    .star {
      width: 1.125rem;
      display: flex;
      align-items: center;
    }
    .star img {
      width: 100%;
    }
    &-link {
      display: block;
      font-size: 0.5625rem;
      line-height: 130%;
      margin-top: 0.125rem;
    }
  }
  &__rating-text {
    font-size: 1.3125rem;
    line-height: 130%;
    font-weight: 700;
    color: #827F7F;
    margin-left: 0.1875rem;
  }
  &__location {
    font-size: 0.875rem;
    line-height: 130%;
    margin-right: 4.375rem;
    span {
      display: block;
      line-height: 130%;
      font-weight: 700;
      margin-bottom: 0.3125rem;
    }
  }
  &__group {
    display: flex;
    align-items: center;
    margin-right: 1.25rem;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-right: 1.25rem;
  }
  &__social {
    width: 2.25rem;
    height: 2.25rem;
    svg {
      width: 100%;
      height: 100%;
    }
    svg path {
      transition: all 0.2s;
    }
    &:hover {
      svg path:nth-child(1) {
        fill: #F2A22E;
      }
      svg path:nth-child(2) {
        fill: #ffffff;
      }
    }
  }
  &__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 700;
    &:hover {
      color: #F2A22E;
    }
  }
  &__btn {
    width: 11.25rem;
    height: 3.125rem;
    span {
      font-size: 0.875rem !important;
    }
  }
  .close-menu {
    display: none;
  }
}
.burger {
  position: relative;
  width: 2.5rem;
  height: 1.5625rem;
  cursor: pointer;
  display: none;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 0.1875rem;
    background: #fff;
    border-radius: 0.1875rem;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
}
.menu-bg {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}

@media screen and (max-width: 1400px) {
  .header {
    &__location {
      margin-right: 1.25rem;
    }
    &__group {
      display: block;
    }
    &__socials {
      margin: 0 0 0.75rem 0;
    }
  }
}

@media screen and (max-width: 1220px) {
  .header {
    &__logo {
      margin-right: 0;
    }
    &__yandex {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 1.5rem 0;
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 18.75rem;
      height: 100%;
      flex-direction: column;
      align-items: start;
      background: #002B45;
      padding: 5rem 1rem 2rem;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
    }
    &__location {
      margin: 0 0 2rem 0;
    }
    &__group {
      margin: 0 0 2rem 0;
    }
    .close-menu {
      position: absolute;
      top: 1rem;
      right: 1rem;
      display: block;
      width: 1.875rem;
      height: 1.25rem;
      cursor: pointer;
      span {
        position: absolute;
        left: 0;
        width: 100%;
        height: 0.125rem;
        background: #ffffff;
      }
      span:nth-child(1) {
        top: 0.5625rem;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        bottom: 0.5625rem;
        transform: rotate(45deg);
      }
    }
  }
  .burger {
    display: block;
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__right {
      width: 100%;
    }
  }
}